import * as React from 'react';

import Translate from '../../common/Translate';

import { vh } from '../../../shared/utils';

import cvpdf from '../../../assets/cv.pdf';

import './hero_desktop.scss';
import './hero_tablet.scss';
import './hero_phone.scss';


type StateType = {
    offsetY: number,
    installEvent: BeforeInstallPromptEvent | null
};


class Hero extends React.Component<{}, StateType>
{
    height: number;

    constructor(props: Readonly<{}>)
    {
        super(props);

        this.scrollListener = this.scrollListener.bind(this);
        this.height = vh(100);

        this.state = {
            offsetY: 0,
            installEvent: null
        };
    }

    getOffsetY(scrollY: number): number
    {
        return scrollY < this.height ? scrollY * 0.7 : this.height;
    }

    scrollListener(): void
    {
        let offsetY = this.getOffsetY(window.scrollY);
        if (offsetY !== this.state.offsetY) {
            this.setState({ offsetY: offsetY, installEvent: this.state.installEvent });
        }
    }

    beforeInstallPromptListener(e: BeforeInstallPromptEvent): void
    {
        this.setState({ offsetY: this.state.offsetY, installEvent: e });
    }

    async install(): Promise<void>
    {
        const installEvent = this.state.installEvent;
        if (!installEvent) {
            return;
        }
        this.state.installEvent.prompt();
        const { outcome } = await this.state.installEvent.userChoice;
        if (outcome === 'accepted') {
            this.setState({ offsetY: this.state.offsetY, installEvent: null });
        }
    }

    render()
    {
        return (
            <div id="hero" className="section">
                <div className="background" style={{backgroundPositionY: this.state.offsetY}}></div>
                <div className="main">
                    <h1>Gianvito Difilippo</h1>
                    <h2><Translate selector="hero:h2"/></h2>
                    <a href="mailto:gianvito.difilippo@gmail.com" className="noselect">
                        <Translate selector="hero:contactme"/>
                    </a>
                    <a href={cvpdf} target="_blank" className="noselect">
                        <Translate selector="hero:downloadcv"/>
                    </a>
                    {
                        !!this.state.installEvent ?
                        <a className="noselect" onClick={async () => await this.install()}>
                            <Translate selector="hero:installapp"/>
                        </a>
                        : null
                    }
                </div>
            </div>
        );
    }

    componentDidMount(): void
    {
        //window.addEventListener('beforeinstallprompt', this.beforeInstallPromptListener);

        document.addEventListener('scroll', this.scrollListener);
        this.scrollListener();
    }

    componentWillUnmount(): void
    {
        //window.removeEventListener('beforeinstallprompt', this.beforeInstallPromptListener);

        document.removeEventListener('scroll', this.scrollListener);
    }
}

export default Hero;